import React, { Component } from 'react'
import ImageCard from '../../components/widgets/imageCard'
import background from '../../images/background44.jpg'
import Card1 from '../../images/all2.jpg'
import Card2 from '../../images/all4.jpg'
import Card3 from '../../images/am.jpg'
import ImageText from '../../components/widgets/imageText'


export default class Home extends Component {
    render() {
        return (
            <div>
                <div className="row">
                    <div className="col-12 carousel-inner com-sm-12">
                        <img className="bgimage" src={background} alt="background" width="100vw"/>
                        <div className="carousel-caption">
                            <h1 className="display-2">A wonderfull Gift</h1>
                            <h3>Here you can find your own CAR!</h3><br/><br/><br/>
                        </div>
                    </div>
                </div>
                {/* <!--- Jumbotron --> */}
                <div className="row ">
                    <div className="col-10">
                        <p className="jumbotron-text">Find out just why it’s called “car city”, and find perfect work here !</p>
                    </div>
                    <div className="col-2 btnMore"  >
                        <a href="#">
                            <button type="button" className="btn btn-outline-secondary ">View More</button>
                        </a>
                    </div>
                </div>

                <div className="row mt-4">
                    <div className="col-2"></div>
                    <div className="col-4">
                       <ImageCard cardimage={Card1} />
                    </div>
                    <div className="col-4"  >
                        <ImageText 
                            h1={'OUR MISSION'}
                            p={'The workshop is big business of car maintenance and repairing in the present time. So our target is to make an automated system of the workshop. It helps both the owner and their customers by providing the online platform. This automation also contains record of their customers on any database of the system. Our main target is to save time of both sides by booking an online appointment.'}
                        />
                    </div>
                    <div className="col-2"></div>
                </div>
                <div className="row mt-4" >
                    <div className="col-2"></div>
                    <div className="col-4"  >
                        <ImageText 
                            h1={'OUR VISION'}
                            p={'"OUR VISION IS TO CONTINUE TO GROW OUR CUSTOMER BASE WHICH IN TURN WILL EXPAND OUR BUSINESS"'}    
                        />
                    </div>
                    <div className="col-4">
                       <ImageCard cardimage={Card2} />
                    </div>
                    <div className="col-2"></div>
                </div>
                <div className="row mt-4">
                    <div className="col-2"></div>
                    <div className="col-4">
                       <ImageCard cardimage={Card3} />
                    </div>
                    <div className="col-4"  >
                        <ImageText 
                            h1={'OUR CORE VALUES'}
                            p='Our core values are judged from our work at workshop, we provide the quality of work with a well experienced employees.'
                        />
                    </div>
                    <div className="col-2"></div>
                </div>

            </div>
        )
    }
}
